.ditty-editor__tabs {
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  //gap: 1px;
  height: 40px;
  font-size: 14px;
  background: #e8e9eb;
}
.ditty-editor__tab {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 62px;
  font-weight: 600;
  padding: 0 15px;
  text-align: center;
  text-decoration: none;
  color: #616970;
  background: #f2f3f5;
  border-radius: 0;
  border: none;
  cursor: pointer;
  transition: color 0.25s ease, background-color 0.25s ease;
  &:hover {
    &:not(.ditty-editor__tab--active) {
      color: #fff;
      background-color: $green;
    }
  }
  &--active {
    width: auto;
    color: $green;
    padding-left: 30px;
    padding-right: 30px;
    background-color: #fff;
    .ditty-editor__tab__label {
      display: block;
    }
    .ditty-editor__tab__icon {
      display: none;
    }
  }
  &__icon {
    flex: 0 0 auto;
    display: block;
    font-size: 18px;
  }
  &__label {
    flex: 1;
    display: none;
  }
}
.ditty-editor__tabs--primary {
  height: 60px;
  background: $dark_gray;
  .ditty-editor__tab {
    color: rgba(255, 255, 255, 0.8);
    background: $dark_gray;
    &:hover {
      &:not(.ditty-editor__tab--active) {
        color: #fff;
        background-color: rgba(255, 255, 255, 0.2);
      }
    }
    &--active {
      color: #fff;
      background-color: $green;
    }
  }
}
.ditty-editor__tabs--secondary {
  border-bottom: 1px solid #ccc;
}
.ditty-editor__tabs--cloud {
  flex-wrap: wrap;
  height: auto;
  background: $dark_gray;
  .ditty-editor__tab {
    width: 25%;
    height: 60px;
    flex: 0 0 auto;
    flex-direction: column;
    align-items: center;
    color: rgba(255, 255, 255, 0.6);
    background: transparent;
    padding: 5px;
    &:hover {
      background: rgba(255, 255, 255, 0.1);
      background: transparent;
    }
    &--active {
      width: 25%;
      .ditty-editor__tab__icon {
        color: $green;
        display: block;
      }
      .ditty-editor__tab__label {
        color: #fff;
      }
    }
    &__icon {
      height: 20px;
      flex: 0 0 auto;
    }
    &__label {
      display: block;
      font-size: 11px;
      font-weight: 400;
      margin-top: 5px;
      flex: 0 0 auto;
    }
  }
}
